
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { isNullValue, getImageUrl, compressImage, createBlob, fileSize, getYMDFormateTime, getTimestamp } from "../../../js/utility.js"
import event from '../../../js/Event.js'
import { AjaxHttp, AjaxImageHttp } from '../../../js/ajaxHttp'
import $ from 'jquery'
import { mapState } from 'vuex'

export default {
components: {
XTextarea,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data() {
return {
height: 100,
files: [],
content: '',
uploadfile: false, //如果为true 不能保存日志
canSave: true,
}
},
beforeDestroy() {
event.$off("saveDone");
},
mounted() {
this.height = window.innerHeight - 264;
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));

if (this.$route.params.action === 'edit') {
this.content = this.logMDetail.content;
this.files = this.logMDetail.file;
}

},
computed: {
...mapState({
logListInfo: state => state.logListInfo,
logMDetail: state => state.logDetail,
}),
},
methods: {
saveInfos() {
if (this.uploadfile) { return; }
if (!this.canSave) { return; }
this.canSave = false;

var postfiles = [];
for (var i = 0; i < this.files.length; i++) {
var file = this.files[i];
postfiles.push(file.file_id);
}

if (this.content === '') {
this.$vux.toast.text('日志内容不能为空', 'top');
return;
}

var url = ''
var params = {};
if (this.$route.params.action === 'add') {
url = 'm=log&a=mylog_add';
params = { category_id: this.$route.params.type, subject: getYMDFormateTime(getTimestamp(new Date())) + ' 工作日志', content: this.content, file: postfiles };
} else if (this.$route.params.action === 'edit') {
url = 'm=log&a=mylog_edit';
params = { category_id: this.$route.params.type, subject: this.logMDetail.subject, content: this.content, file: postfiles, id: this.logMDetail.log_id };
}

AjaxHttp('POST', url, params, this, function(res, wself, success) {
if (success) {
wself.logListInfo.refresh = true;
wself.logListInfo.showFileList = false;
wself.$store.commit('updateLogListInfo', wself.logListInfo);
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
wself.canSave = true;
}
});
},
uploadimg(e) {
console.log(e);

var src, url = window.URL || window.webkitURL || window.mozURL,
files = e.target.files;
for (var i = 0, len = files.length; i < len; ++i) {
var file = files[i];
var self = this;
compressImage(file, 0.3, (code) => {
var formdata = new FormData();
var blob = createBlob(code);
formdata.append('file', blob, file.name);
if (self.$route.params.action === 'edit') { //编辑日志下 通过传ID直接与日志绑定
formdata.append('module', 'log');
formdata.append('r', 'rFileLog');
formdata.append('id', self.logMDetail.log_id);
}
self.uploadfile = true; 
AjaxImageHttp('POST', 'm=file&a=add', formdata, self, function(res, wself, success) {
if (success) {

wself.files.push({ 'name': file.name, 'size': fileSize(blob.size), 'file_path': code, 'file_id': res.data, 'file_type': 'jpg', 'local': true });
wself.uploadfile = false; 
}
});
});
}
e.target.value ="";
},









previewerImg(images, imgindex) {
var previewerlist = [];
for (var i = 0; i < images.length; i++) {
var item = images[i];
previewerlist.push({
src: item.file_path
});
}
event.$emit('previewerAction', { list: previewerlist, index: imgindex });
},
addFiles() {
if ($(fileElem)) {
$(fileElem).click();
}
},
onSwipeoutClick(type, item, index) { //edit delete
if (type === 'delete') {
AjaxHttp('POST', 'm=file&a=delete', {
id: item.file_id,
module: 'log'
}, this, function(res, wself, success) {
if (success) {
if (res.status === 1) {
wself.files.splice(index, 1);
}
}
});
}
},
fileTypeImage(item) {
var type = item.file_type;
if (type === 'doc' || type === 'DOC' || type === 'docx') {
return require("../../../assets/file/doc.png");
} else if (type === 'xls' || type === 'xlsx') {
return require("../../../assets/file/excel.png");
} else if (type === 'png' || type === 'gif' || type === 'jpg' || type === 'jpeg') {
if (item.local === true) {
return item.file_path;
} else {
return getImageUrl(item.file_path);
}
} else if (type === 'pdf' || type === 'PDF') {
return require("../../../assets/file/pdf.png");
} else if (type === 'ppt' || type === 'PPT' || type === 'pptx') {
return require("../../../assets/file/ppt.png");
} else if (type === 'txt' || type === 'TXT') {
return require("../../../assets/file/txt.png");
} else if (type === 'zip' || type === 'ZIP' || type === 'rar') {
return require("../../../assets/file/zip.png");
}
return require("../../../assets/file/unknown.png");
},
}
}




